@import "../framework/sass/utils/layout";

@import "../framework-plus/sass/utils/reset";

@include full-screen();
@include reset-body-marigin();

body {
    background: rgb(10,10,30);
    width: 1px;
    margin: 300px auto;
    position: relative;
}

body div {
    animation: anim 1s linear infinite alternate;
    animation-fill-mode: backwards;
    position: absolute;
    top: 0;
    left: 0;
    transform-origin: top center;
}

$delay: 0.01s;
$same: $delay;

@for $n from 0 to 360 {
    body div:nth-child(#{$n + 1}) {
        animation-delay: $delay;
        background: hsl($n, 95%, 65%);
        transform: rotate($n * 1deg) translate(100px);
    }
    $delay: $delay + $same;
}

@keyframes anim {
    0% {
        height: 100px;
        width: 5px;
    }
    100% {
        transform: rotate(180deg) translate(-200px,-150px) rotate(-45deg);
        height: 500px;
        width: 1px;
    }
}